
import {defineComponent, ref, Ref, watch, onMounted} from 'vue';
import {useCookies} from '@vueuse/integrations/useCookies'
import Ability from "@/components/elements/Ability.vue";
import Data from '@/ability.json';
import {Ability as AbilityType, Effects} from '@/types/Ability'

export default defineComponent({
    components: {Ability},
    setup() {
        const favorites: Ref<Array<string>> = ref([]);
        const favoriteItems: Ref<AbilityType[]> = ref([]);
        const cookies = useCookies();
        const effects: Effects = {
            'attack-up': ' <b></b>   20%.     .',
            'attack-down': '      20%.   <b>   </b>.   .',
            'speed-up': '   20%,  1  2  (   ).     .',
            'speed-down': '   20%  2      .    .',
            'morale-up': '   <b> </b>  20%.     .',
            'morale-down': '   20%  2     .    .',
            'aroma': ',     Aroma     .      ,    Aroma.      .',
            'chill': 'Axie,   Chill,      <b>Last Stand</b>.       .',
            'fear': '  Fear ,   ,  .   -      Fear    1  2 .',
            'fragile': ' ,  Fragile,        .     ,         .',
            'jinx': '  Jinx           .',
            'lethal': '      .   <b>     </b>.',
            'poison': '       2  <b>    </b>.   .',
            'sleep': '    ,     HP Axie.     .',
            'stench': '  <b> </b>  ,   Stench.     .',
            'stun': ',   Stun     .        <b></b>   ,    Stun, <b> </b>.   <b>  </b>.',
            'prioritize': '   .',
            'untargetable': '   ',
            'remove-debuff': '  .',
            'gain-energy': ' ,     .',
            'disable-ability': ' ,       .',
            'end-last-stand': '  last stand.',
            'raise-damage': '    -.',
            'discard': '      ,    .',
            'damage-reflect': '   .',
            'double-hit': '    .',
            'multi-hit': '     .',
            'self-heal': '     - ,    .',
            'raise-shield': ' ',
            'fixed-damage': ' ',
            'draw-card': '    '
        };

        const abilities = (Data as AbilityType[]).map((item) => {
            item.effect_description = effects[item.effect];

            return item;
        });

        const addFavorite = (key: string) => {
            if (favorites.value.some(_key => _key === key)) {
                favorites.value = favorites.value.filter(_key => _key !== key);
            } else {
                favorites.value.push(key);
            }

            favorites.value = favorites.value.filter((v, i, a) => a.indexOf(v) === i);

            cookies.set('favorites', favorites.value);
        }


        watch(() => favorites.value, () => {
            console.log('test');
            try {
                favoriteItems.value = favorites.value.map((key: string) => {
                    const item = abilities.find(item => item.key === key);

                    if (item === undefined) {
                        throw new Error('Not found item');
                    }

                    return item;
                });
            } catch (e) {
                cookies.remove('favorites');
            }
        });

        onMounted(() => {
            if (cookies.get('favorites') !== undefined) {
                favorites.value = cookies.get('favorites');
            }
        });


        return {abilities, addFavorite, effects, favoriteItems, favorites};
    }
});
